/* login page */
// Admin
.admin {
  background-color: #F8F9FB;
  position: relative;
  @include sm{
    overflow-x: hidden;
  }
  .edit-profile {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include md{
      height: 100%;
      padding-top: 60px;
      padding-bottom: 60px;
      position: relative;
      z-index: 2;
    }
    .card {
      border-radius: 6px;
      box-shadow: 0 5px 20px rgba(var(--shadow-main), 0.11);
      .card-header {
        border-bottom: 1px solid var(--border-color);
        justify-content: center;
        align-items: center;
        min-height: 70px;
        padding: 20px 40px;

        h4 {
          @include rfs(22px);
          font-weight: 600;
        }
      }

      .card-body {
        padding: 30px 40px 28px 40px;

        @include ssm {
          padding: 30px 25px 23px 25px;
        }

        .edit-profile__body {
          .form-group {
            .form-control {
              color: var(--color-dark);
              box-shadow: 0 5px 20px transparent;
              transition: var(--transition);

              &::placeholder {
                color: var(--color-lighten);
              }
            }
          }
        }
      }
    }
  }

  .edit-profile__logos {
    display: flex;
    justify-content: center;
    margin-bottom: 25px;

    .light {
      display: none;
    }

    img {
      width: 172px;
      max-width: 100%;
    }
  }
}

.admin-element {
  background-color: var(--color-white);
}

.admin-topbar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0 30px;

  p {
    color: var(--color-gray);
    @include ofs(14px, lh(14px, 20px), 500);
  }
}

.admin-element-right {
  .edit-profile {
    @media (min-width: 1600px) {
      margin: 0 99px;
    }
  }

  .card .card-header {
    @include sm {
      justify-content: center;
    }

    h6 {
      @include ofs(24px, lh(24px, 30px), 600);
      color: var(--color-dark);
      display: flex;
      align-items: center;

      span {
        color: var(--color-primary);
        @include ofs(24px, lh(24px, 30px), 600);
        margin: 0;
        margin-left: 6px;
      }
    }
  }

  .edit-profile__body .form-group .form-control::placeholder {
    color: var(--color-lighten);
  }
}

.admin-header-top {
  p {
    @include ofs(14px, lh(14px, 22px), 400);
    color: var(--color-gray);
  }
}

.admin-condition {
  margin-bottom: 2px;

  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: capitalize;
  margin-bottom: 19px;
  margin-top: 23px;

  @include ssm {
    margin-bottom: 14px;
    margin-top: 18px
  }

  @include xxs {
    margin-bottom: 10px;
    margin-top: 15px;
    flex-flow: column;

    a {
      margin-top: 10px;
    }
  }

  .custom-checkbox {

    input[type="checkbox"]+label:before,
    input[type="checkbox"]+label:after {
      margin-top: 0;
    }
  }

  a {
    @include ofs(13px, lh(13px, 22px), 400);
    color: var(--color-primary);
  }

  .custom-checkbox {
    .checkbox-text {
      @include ofs(13px, lh(13px, 22px), 400);
      color: var(--color-light);
      cursor: pointer;
    }

    input[type="checkbox"]+label:before {
      top: 10px;
      left: 10px;
      transform: translate(-50%, -50%);
    }

    input[type="checkbox"]+label:after {
      width: 20px;
      height: 20px;
    }
  }
}

.admin-socialBtn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  li {
    button {
      box-shadow: none;
      background: var(--bg-normal);
      border: none;
      white-space: nowrap;
      width: 48px;
      height: 48px;
      padding: 0;
      border-radius: 5px;
      transition: var(--transition);
      @include sm{
        width: 40px;
        height: 40px;
      }

      &:hover {
        opacity: 0.7;
      }

      svg {
        width: 20px;
      }

      i {
        font-size: 22px;
      }

      &.google {
        background-color: #F06548;
      }

      &.facebook {
        background-color: rgba(#3A589B, 0.10);

        i {
          color: #3A589B;
        }
      }

      &.twitter {
        background-color: rgba(#03A9F4, 0.10);

        i {
          color: #03A9F4;
        }
      }

      &.github {
        background-color: rgba(#090E30, 0.10);

        i {
          color: #090E30;
        }
      }


    }

    img,
    svg {
      margin-right: 0;
      width: 20px;
      height: 20px;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}

//Social Connector
.social-connector {
  overflow: hidden;
  text-align: center;
  position: relative;

  span {
    @include ofs(13px, lh(13px, 22px), 500);
    color: var(--color-light);
  }

  &.social-connector__admin {
    margin-bottom: 27px;

    @include ssm {
      margin-bottom: 22px;
    }
  }
}

.social-connector:before,
.social-connector:after {
  background-color: var(--border-normal);
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 50%;
}

.social-connector:before {
  right: 0.5em;
  margin-left: -50%;
}

.social-connector:after {
  left: 0.5em;
  margin-right: -50%;
}

.admin-createBtn {
  width: 185px;
}

.signIn-createBtn {
  border-radius: 6px;
  white-space: nowrap;
  min-height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  @include sm{
    min-height: 40px;
  }
}

//Sign Up Overlay
.adminTop {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(-1);
  /*rtl:begin:remove*/
  transform: scaleX(1);
  /*rtl:end:remove*/
}

.adminBottom {
  position: absolute;
  right: 0;
  top: 0;
  transform: scaleX(-1);
  /*rtl:begin:remove*/
  transform: scaleX(1);
  /*rtl:end:remove*/
}


//Edit Profile
.edit-profile__body {
  label {
    text-transform: capitalize;
    margin-bottom: 8px
  }

  .form-group {
    label {
      @include ofs(14px, lh(14px, 20px), 500);
      color: var(--color-dark);
    }

    .form-control {
      border: 1px solid var(--border-color);
      border-radius: 4px;
      display: flex;
      align-items: center;
      padding: 10px 20px;
      box-shadow: none;
      transition: 0.3s;

      &::placeholder {
        @include ofs(14px, lh(14px, 25px), 400);
        color: var(--color-gray);
      }

      &:hover {
        border-color: var(--color-primary);
      }
    }

    textarea.form-control {
      height: auto;
      resize: none;
    }
  }
}

.uil-eye-slash {
  &.uil-eye {
    &:before {
      content: '\ebaf' !important;
    }
  }
}

//Edit Social Profile
.edit-profile__body {
  .form-control {
    height: 48px;
    @include sm{
      height: 40px;
    }

    &--social {
      height: 44px;
      padding: 0.375rem 0.9375rem;

      &::placeholder {
        color: var(--light-gray);
      }
    }
  }
}

/* sign In Left */
.admin-left{
  background-color: rgba(var(--color-primary-rgba), 0.20);
  height: 100vh;
  padding: 147px 75px;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  @include md{
    position: absolute;
    background-color: transparent;
    height: 100%;
    width: 100%;
  }

  @include sm {
    height: 540px;
    padding: 30px 30px;
    display: flex;
    justify-content: center;
    text-align: center;
  }
  @include xs{
    height: 430px;
  }
  @include e(img) {
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      width: 100%;
    }
  }
  .left-shape{
    position: absolute;
    top:0;
    left: 0;
    z-index: -1;
    svg{
      width: 100%;
      color: var(--color-primary);
      @include md{
        width: calc(100% - 10%);
      }
    }
  }
  .middle-img{
    img{
      @include md{
        display: none;
      }
    }
  }
}

//trigger
.dark-trigger {
  position: fixed;
  right: 30px;
  top: 30px;
  transform: translateY(-50%);
  z-index: 999;
  margin: 0;
  color: #fff;
  transition: var(--transition);

  ul {
    li {
      a {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        line-height: 63px;
        background: var(--color-warning);
        border-radius: 50%;
        font-size: 30px;
        color: #fff;
        transition: .5s;

        i {
          font-size: 22px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: var(--color-warning);
          transition: .5s;
          transform: scale(.9);
          z-index: -1;
        }

        &.active,
        &:hover {
          color: #ffee10;
          text-shadow: 0 0 5px #ffee10;

          &::before {
            transform: scale(1.1);
            box-shadow: 0 0 15px var(--color-warning);
          }
        }
      }
    }
  }
}